import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { v2 } from '../utils/data';

const URL_V2_BASE: string = "https://fulgurant-spins.000webhostapp.com/";
const URL_V2_REST: string = URL_V2_BASE + "wp-json/wp/v2/";
const URL_V2_FAT_SECRET: string = URL_V2_BASE + "wp-json/prc/v1/nutrition_data/";
const URL_V2_TERM_IMAGE: string = URL_V2_BASE + "wp-json/prc/v1/term_image/";

const queryBuilderV2 = function(config: any = {}) {
  config = {
    per_page: 10,
    page: 1,
    ...config
  }
  let url = URL_V2_REST;
  const endpoint = config.endpoint;
  const id = config.id;
  const relationships = config.relationships;

  config.per_page = config.limit || 10;

  config.page = (config.skip) ? parseInt(config.skip) : 1;

  delete config.endpoint;
  delete config.id;
  delete config.relationships;
  delete config.limit;
  delete config.skip;

  if (!endpoint) throw "Need a rest api endpoint";
  url += endpoint;
  if (id) {
    url += '/' + id;

  } else {
    url += "?";
    for (let key of Object.keys(config)) {
      url += key + "=" + config[key] + "&";
    }
  }
  return url;

}


@Injectable({
  providedIn: 'root'
})
export class DataV2Service {

  constructor(protected http: HttpClient, ) { }

  async getCategories(config: any = {}) {
    let cuisines: any;
    let courses: any;

    config.limit = 100;
    config.endpoint = "wprm_cuisine";

    cuisines = await this.http
      .get(queryBuilderV2(config))
      .toPromise();

    config.endpoint = "wprm_course";
    courses = await this.http
      .get(queryBuilderV2(config))
      .toPromise();

    let categories = [].concat(v2.parseCategoryCuisines(cuisines)).concat(v2.parseCategoryCourses(courses))
    return await Promise.all(categories.map(async (el) => {
      let thumbnail = await this.http.get(URL_V2_TERM_IMAGE + el.id).toPromise();
      if (thumbnail) {
        el.thumbnail = URL_V2_BASE + thumbnail;
      } else {
        el.thumbnail = "/assets/images/category-1.png";
      }
      return el;
    }));
  }

  async getRecipes(config: any = {}) {
    config.endpoint = "wprm_recipe";
    let results: any = await this.http
      .get(queryBuilderV2(config))
      .toPromise();

    return (config.id) ? v2.parseRecipe(results) : results.map(el => v2.parseRecipe(el));
  }

  async getIngredients(config: any = {}) {
    config.endpoint = "wprm_ingredient";
    let results: any = await this.http
      .get(queryBuilderV2(config))
      .toPromise();

    if (config.id) {
      results = await this.getNutritionData(results);
      return v2.parseIngredient(results)
    } else {
      for (let result of results) {
        result = await this.getNutritionData(result);
      }
      return results.map(el => v2.parseIngredient(el));
    }

    return (config.id) ? v2.parseIngredient(results) : results.map(el => v2.parseIngredient(el));
  }

  async getNutritionData(ingredient: any = {}) {
    const nutritionData = await this.http.get(URL_V2_FAT_SECRET + ingredient.id).toPromise();
    ingredient.nutritionData = nutritionData;
    return ingredient;
  }


}
