import { AnimationController } from '@ionic/angular';

let IS_SLIDING_OUT = false;
let IS_SLIDING = false;
const DURATION = 400;
const SLIDE = async function (baseElement: HTMLElement, from: any, to: any) {

    if (IS_SLIDING) return;
    IS_SLIDING = true;
    const animationController = new AnimationController();
    const slide = animationController.create()
        .addElement(baseElement)
        .duration(DURATION)
        .iterations(1)
        .easing('ease-in')
        .fromTo('marginTop', from, to);

    await slide.play();

    IS_SLIDING = false;
}

const OUT = async function (baseElement: HTMLElement) {
    if (IS_SLIDING_OUT) return;
    IS_SLIDING_OUT = true;
    const animationController = new AnimationController();
    const slide = animationController.create()
        .addElement(baseElement)
        .duration(150)
        .iterations(1)
        .easing('ease-in')
        .fromTo('left', '0px', '100vw');

    await slide.play();

    IS_SLIDING_OUT = false;
}

const MARGIN_TOP = function (baseElement) {
    return `-${baseElement.clientHeight}px`;
}
export const headerSlideUpAnimation = function (baseElement: HTMLElement) {
    SLIDE(baseElement, '0px', MARGIN_TOP(baseElement));
}

export const headerSlideDownAnimation = function (baseElement: HTMLElement) {
    SLIDE(baseElement, MARGIN_TOP(baseElement), '0px')
}

export const headerSlideOutAnimation = function (baseElement: HTMLElement) {
    OUT(baseElement);
}